import { createRouter, createWebHistory } from 'vue-router';
// import store from '@js/AutomaticLottery/store';
import lotteryDinner from '@comp/AutomaticLottery/MainPage/LotteryDinner.vue';
const routes = [
  {
    path: "/",
    redirect: "/lotteryDinner",
  },
  // Â¶ÇÊûú‰∫ÇÊâì/Á∂≤ÂùÄÊúÉÂ∞éÂêëÁöÑÈ†ÅÈù¢
  {
    path: "/:pathMatch(.*)*",
    name: "notFoundRedirect",
    redirect: "/lotteryDinner",
  },
  {
    path: "/lotteryDinner",
    name: "lotteryDinner",
    component: lotteryDinner,
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

