import _ from 'lodash';
function interfaceData() {
    return {
        // Âú∞ÈªûÊ∏ÖÂñÆ
        placesList: [
            'ÈçãÊπØÊúÉ',
            'Ë≤¥Êóè‰∏ñÂÆ∂(Ê°ÉÂúí)',
            'ÂçÉËëâÁÅ´Èçã(Êñ∞Á´π)',
            'ÂçÉËëâÁÅ´Èçã(Á´πÂåó)',
            'Á¥ÖÂê±Âê±',
            'Ë≤∑‰æøÁï∂',
            'Ë•øÂ†§ÁâõÊéí',
            'Á´πÊù±ÊéíÈ™®ÈÖ•',
            'ÈáëÂÖÉÂØ∂È§ÉÂ≠êÂ§ßÁéã',
        ], 
        // ÊäΩÁ±§Ê≠∑Âè≤
        history: [], 
        // Èò≤Ê≠¢ÈáçË§áÈñãÈóú
        noRepeat: false, 
    }
}
const state = interfaceData();
const mutations = {
    setNewPlace(state,place) {
        state.placesList.push(place);
        console.log('placesListÊõ¥Êñ∞', state.placesList);
    },
    
    deletePlace(state, placeIdx) {
        state.placesList.splice(placeIdx, 1);
        console.log('placesListÊõ¥Êñ∞', state.placesList);
    },

    setNoRepeat(state,sw) {
        if (sw) {
            state.noRepeat = _.cloneDeep(sw);
        }
        else{
            state.noRepeat = !state.noRepeat
        }
        console.log('noRepeatÊõ¥Êñ∞', state.placesList);
    },

};

const actions = {
    updateNewPlace({ commit },place) {
        commit('setNewPlace',place);
    },
    updateDeletePlace({ commit }, idx) {
        commit('deletePlace', idx);
    },
    updateNoRepeat({ commit }, sw) {
        commit('setNoRepeat', sw);
    },
};

const getters = {
    getPlacesList: (state) => {
        return state.placesList;
    },
    getHistory: (state) => {
        return state.history;
    },
    getNoRepeat: (state) => {
        return state.noRepeat;
    },
}

export default {
    namespaced: true,
    state,
    mutations,
    actions,
    getters
};
