function interface_data() {
    return {
        //----------------ÂçÄÂüüÈñãÈóúÂàóË°®----------------

        LoadingScreenSW: {
            state: false,
        },

        // ÂÅ¥Ê¨ÑÈñãÈóú
        Left_menu_sw: {
            expanded: false,
            combine: false,
        },

        Right_menu_sw: {
            expanded: false,
            combine: false,
        },

        // Information_windowÂçÄÂüü
        Info_window_sw: {
            // ÁèæÂú®È°ØÁ§∫ÁöÑÂçÄÂüü
            show_page: '',

            close_popup: false,
            // ÈÅÆÁΩ©
            all_info_window: false,

            // Ë®äÊÅØÂçÄÂ°ä
            error_window: false,
            error_window_reload: false,
            btn_control_info: false,
            warning_control_info: false,
            success_send_information: false,

            // ÊéßÂà∂btn_controlÂΩàË∑≥Ë¶ñÁ™óÊåâÈàïÁöÑÈ°ØÁ§∫
            confirm: false,
            close: true,
        },

        // Main_area_sw: {
        //     show_page: 'front_page',
        //     front_page: true,
        //     eq_management: false,

        //     sensor_conf: false,
        //     recipe: false,
        //     schedule: false,
        //     ploty_table: false,
        // },

        Create_window_sw: {
            // ÁèæÂú®È°ØÁ§∫ÁöÑÂçÄÂüü
            show_page: '',
            // ÈÅÆÁΩ©
            // all_info_window: false,
            close_popup: false,
            // Ë°®ÂñÆÂçÄÂ°ä
            create_project: false,
            create_group: false,
            create_channel: false,
        },
    }
}
const state = interface_data();

// mutationsË¶èÂâá:Â§ñ‰æÜÂÄºÂè™ËÉΩÂÇ≥‰∏ÄÂÄãÈÄ≤‰æÜÔºåË¶ÅÂÇ≥Â§öÂÄºÈúÄË¶ÅÂú®Â§ñÈÉ®ÊπäÊàêÈô£ÂàóÊàñÂ≠óÂÖ∏„ÄÇ
const mutations = {
    setLoadingScreenSW(state, sw) {
        
        state.LoadingScreenSW.state = sw;
    },
    //LeftÂÅ¥Ê¨ÑÈñãÈóúÂàóË°®
    setLeft_menu_sw(state, sw) {
        // Â¶ÇÊûúÊòØÂ≠ó‰∏≤ÔºåÁõ¥Êé•ÂàáÊèõË©≤Â≠ó‰∏≤ÁöÑÈñãÈóúÂ∏ÉÊûóÂÄº
        if (typeof sw === "string") {
            state.Left_menu_sw[sw] = !state.Left_menu_sw[sw];
        }
        // Â¶ÇÊûúÈùûÂ≠ó‰∏≤(ÊúÉÊòØÁâ©‰ª∂)Ôºå‰æùÁÖßÊåáÂÆöÊ¢ù‰ª∂ÂàáÊèõÈñãÈóúÁöÑÂ∏ÉÊûóÂÄº
        else {
            state.Left_menu_sw[sw.name] = sw.value;
        }
    },
    //RightÂÅ¥Ê¨ÑÈñãÈóúÂàóË°®
    setRight_menu_sw(state, sw) {
        // Â¶ÇÊûúÊòØÂ≠ó‰∏≤ÔºåÁõ¥Êé•ÂàáÊèõË©≤Â≠ó‰∏≤ÁöÑÈñãÈóúÂ∏ÉÊûóÂÄº
        if (typeof sw === "string") {
            state.Right_menu_sw[sw] = !state.Right_menu_sw[sw];
        }
        // Â¶ÇÊûúÈùûÂ≠ó‰∏≤(ÊúÉÊòØÁâ©‰ª∂)Ôºå‰æùÁÖßÊåáÂÆöÊ¢ù‰ª∂ÂàáÊèõÈñãÈóúÁöÑÂ∏ÉÊûóÂÄº
        else {
            state.Right_menu_sw[sw.name] = sw.value;
        }
    },
    setInfo_window_sw(state, popup) {
        if (popup !== 'close_popup') {
            // state.Info_window_sw.all_info_window = true;
            // ÈñãÂïüË©≤ÂçÄÂüü
            state.Info_window_sw[popup.area] = true;
            // Á¢∫Ë™çÊúâÊ≤íÊúâÈôÑÂ∏∂data_checkÁöÑkey
            if ('data_check' in popup) {
                // Ë©≤key ÁãÄÊÖãÊòØtrueÈ°ØÁ§∫ÈÄÅÂá∫(Á¢∫Ë™ç)ÊåâÈàï
                if (popup.data_check === true) {
                    state.Info_window_sw.confirm = true;
                    state.Info_window_sw.close = true;
                }
                else {
                    state.Info_window_sw.confirm = false;
                    state.Info_window_sw.close = true;
                }
            }
            // Á¥ÄÈåÑÁèæÂú®Ë¶ÅÈñãÂïüÁöÑÂçÄÂüü
            state.Info_window_sw.show_page = popup.area;
        }
        else {
            state.Info_window_sw[state.Info_window_sw.show_page] = false;
            // state.Info_window_sw.all_info_window = false;
            state.Info_window_sw.show_page = '';
        }
        console.log(state.Info_window_sw);
    },
    // setMain_area_sw(state, page) {
    //     if (state.Main_area_sw.show_page !== page) {
    //         state.Main_area_sw[state.Main_area_sw.show_page] = false;
    //     }
    //     state.Main_area_sw[page] = true;
    //     state.Main_area_sw.show_page = page;
    //     console.log('Áï´Èù¢ÂàáÊèõ', page);
    // },

    setCreate_window_sw(state, popup) {

        if (popup !== 'close_popup') {
            // ÈñãÂïüË©≤ÂçÄÂüü
            state.Create_window_sw[popup] = true;
            // Á¥ÄÈåÑÁèæÂú®Ë¶ÅÈñãÂïüÁöÑÂçÄÂüü
            state.Create_window_sw.show_page = popup;
        }
        else {
            state.Create_window_sw[state.Create_window_sw.show_page] = false;
            state.Create_window_sw.show_page = '';
        }

        console.log(state.Create_window_sw);

    },
};

const actions = {
    updateLoadingScreenSW({ commit }, sw) {
        commit('setLoadingScreenSW', sw);
    },
    updateLeft_menu_sw({ commit }, sw) {
        commit('setLeft_menu_sw', sw);
    },
    updateRight_menu_sw({ commit }, sw) {
        commit('setRight_menu_sw', sw);
    },
    updateInfo_window_sw({ commit }, popup) {
        commit('setInfo_window_sw', popup);
    },
    updateMain_area_sw({ commit }, sw) {
        commit('setMain_area_sw', sw);
    },
    updateCreate_window_sw({ commit }, popup) {
        commit('setCreate_window_sw', popup);
    },
};
const getters = {
    getLoadingScreenSW: (state) => state.LoadingScreenSW.state,
    getLeft_menu_sw: (state) => state.Left_menu_sw,
    getRight_menu_sw: (state) => state.Right_menu_sw,
    getInfo_window_sw: (state) => state.Info_window_sw,
    getMain_area_sw: (state) => state.Main_area_sw,
    getCreate_window_sw: (state) => state.Create_window_sw,
}
export default {
    // namespacedË®≠ÁΩÆtrueÊôÇ‰æùjsÊ™îÊ°àÂêçÂ≠óÂçÄÂàÜÁõ∏ÂêåÂêçÁ®±ÁöÑËÆäÊï∏„ÄÇ
    namespaced: true,
    state,
    mutations,
    actions,
    getters
};
